<template lang="html">
    <div class="stem">
        <div class="sidebar clear">
            <div class="sidebar-left">
                <ul>
                <router-link :to="{path:'/meth'}" tag="li" active-class="act">企业中心</router-link>
                <router-link :to="{path:'/inquiry'}" tag="li" active-class="act">消息盒子</router-link>
                <router-link :to="{path:'/shared'}" tag="li" active-class="act">修改密码</router-link>
                </ul>
            </div>
            <router-view class="sidebar-right"></router-view>
        </div>
    </div>
</template>
<script>
export default {
}
</script>


<style lang="less" scoped>
    .stem{
        width: 1200px;
        margin: 80px auto;
        .sidebar{
            width: 1200px;
            background: #FAFBFF;
            margin: -80px auto;
                .sidebar-left{
                    float: left;
                    width: 200px;
                    height: 1000px;
                    background: #fff;
                    position: relative;
                    ul{
                        height: 100%;
                        border: 1px solid #e8e6f3;
                        li{
                        height: 50px;
                        border-bottom: 1px solid #e8e6f3;
                        text-align: center;
                        line-height: 50px;
                        color: #717171;
                        font-size: 16px;
                        }
                        .act{
                        color: #2e53ac;
                        background: #f5f6fb;
                        font-weight: 700;
                        }
                    }
                }
                .sidebar-right{
                    width: 990px;
                    height: 1000px;
                    float: right;
                }
        }
        .waybill-header{
            height: 90px;
            width:800px;
            display: flex;
            justify-content: space-between;
            line-height: 90px;
            /*border:1px solid #ccc;*/
            
        }
        .waybill-header .header-zuo{
            display: flex;
            
        }
        .waybill-header .header-zuo h2{
                color: #4046b6;
                margin-right: 30px;
            }
        .waybill-header p{
            color: #ccc;
            font-size: 13px;
        }
    }

</style>


